body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Layout__Layout--2C2_c {
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.Layout__Layout--2C2_c main {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}
.Quiz__Quiz--3J8Ip {
    display: flex;
    justify-content: center;
    padding-top: 100px;
    flex-grow: 1;
    width: 100%;
    background: -webkit-linear-gradient(left, #5041b2 0%, #7969e6 100%);
    background: linear-gradient(90deg, #5041b2 0%, #7969e6 100%);
}

.Quiz__Quiz--3J8Ip h1 {
    color: white;
    margin-left: 10px;
}

.Quiz__QuizWrapper--2cJp0 {
    width: 600px;

}
.ActiveQuiz__ActiveQuiz--1xPLH {
    padding: 20px;
    color: #ffffff;
    border: 2px solid #fff;
    border-radius: 5px;
    margin: 0 10px;
    box-sizing: border-box;
}

.ActiveQuiz__Question--2AtZS {
    display: flex;
    justify-content: space-between;
}


.AnswersList__AnswersList--1WLwo {
    list-style: none;
    margin: 0;
    padding: 0;

}
.AnswerItem__AnswerItem--FMFZL {
    border: 1px solid #fff;
    border-radius: 5px;
    padding: 5px 10px;
    margin-bottom: 5px;
    cursor: pointer;
}
.AnswerItem__AnswerItem--FMFZL:hover {
    background: rgba(255, 255,255, .2);
    -webkit-transition: background .3s ease-in-out;
    transition: background .3s ease-in-out;
}

.AnswerItem__AnswerItem--FMFZL.AnswerItem__success--tAKqq {
    background: rgba(161, 240, 69, .7);
}

.AnswerItem__AnswerItem--FMFZL.AnswerItem__error--27Deq  {
    background: rgba(240, 87, 108, .7);
}
